body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}
#home,
#list,
#detail,
#content {
  padding-bottom: 40px;
  overflow-x: hidden;
}
#footers {
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: #f4f4f4f4;
  line-height: 40px;
  font-size: 12px;
  width: 100%;
}
#footers .btn {
  text-align: center;
  font-size: 12px;
}
.slick-slider img {
  display: block;
  width: 100%;
}
.adsbygoogle {
  display: none;
}
header .btn.active {
  background-color: #333;
  color: #fff;
}
header .btn {
  text-align: center;
  font-size: 14px;
  line-height: 40px;
  color: #666;
}
header .btn svg {
  margin-right: 10px;
}
